.text-ellipsis {
  overflow: hidden; /* 넘치는 내용 숨기기 */
  white-space: nowrap; /* 텍스트를 한 줄로 표시 */
  text-overflow: ellipsis; /* 말줄임표 표시 */
}

.checked {
  font-weight: bold;
  border-radius: 100px;
  color: #3584ce
}

.clicked {
  font-weight: bold;
  border-radius: 100px;
  color: #3584ce !important;
}

.base {
  color: rgb(71, 71, 71);
}

.example-spacer {
  flex: 1 1 auto;
}